'use client'

interface WhatsAppButtonProps {
  className?: string
  variant?: 'primary' | 'secondary'
  size?: 'sm' | 'md' | 'lg'
}

export default function WhatsAppButton({ 
  className = '', 
  variant = 'primary',
  size = 'md'
}: WhatsAppButtonProps) {
  const phoneNumber = '5493512703003'
  const message = 'Hola, me gustar√≠a agendar un turno para manicura/pedicura'
  const whatsappUrl = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message)}`

  const baseClasses = 'inline-flex items-center justify-center font-medium transition-all duration-300 rounded-full shadow-lg hover:shadow-xl transform hover:-translate-y-1'
  
  const variants = {
    primary: 'bg-gradient-to-r from-pink-300 to-pink-400 hover:from-pink-400 hover:to-pink-500 text-white',
    secondary: 'bg-white hover:bg-pink-50 text-pink-400 border-2 border-pink-300 hover:border-pink-400'
  }

  const sizes = {
    sm: 'px-4 py-2 text-sm',
    md: 'px-6 py-3 text-base',
    lg: 'px-8 py-4 text-lg'
  }

  const handleClick = () => {
    window.open(whatsappUrl, '_blank')
  }

  return (
    <button
      onClick={handleClick}
      className={`${baseClasses} ${variants[variant]} ${sizes[size]} ${className}`}
    >
      <span className="mr-2">üì±</span>
      Agendar Turno
    </button>
  )
}
